import mis from '../components/mkl.js'
export default {
  name: 'BawetxKdbfn',
  mixins: [mis],
  components: { vuedraggable },

  data() {
    return {
      activeTab: 0,
      isShow: false,
      list1: [
        {
          id: 4,
          name: '哔哩哔哩',
          imgs: '	https://files.codelife.cc/topsearch/74KvxwokxM.png',
