        },
        {
          id: 7,
          name: '腾讯',
          imgs: '		https://files.codelife.cc/topsearch/12owgX0oNV.png',
          content: '腾讯新闻·热问'
        }
      ],
      mis: 'mis',
      that: ''
    }
  },

  created() {
    return this.list
  },
  methods: {
    search(that) {
      return this.list1.filter(item => {
        if (item.name.includes(that)) {
          return item
        }
      })
    },

    changeTab(index1) {
      this.isShow = !this.isShow
      this.activeTab = index1
    },
    getBoxColor(inde) {
      if (inde === 0) {
        return '#fe2d46'
      } else if (inde === 1) {
        return '#ff6600'
      } else if (inde === 2) {
        return '#faa90e'
      }
    }
  }
}
</script>

